

import React from 'react'
import {
  AiFillFacebook,
  AiFillTwitterSquare,
  AiFillInstagram,
  AiFillLinkedin,
} from 'react-icons/ai';  


export default function Footer() {
  return (
    <footer className="bg-dark text-white py-4" >
      <div className="container ">
        <div className="row">
          <div className="col-md-4">
            <h4 className="text-danger">Pizza Palace</h4>
            <ul className="list-unstyled">
              <li>
                <a href="/about">About Us</a>
              </li>
             
              <li>
                <a href="/privacypolicy">Privacy Policy</a>
              </li>
            </ul>
          </div>
          <div className="col-md-4">
            <h4 className="text-danger">Get Help</h4>
            <ul className="list-unstyled">
            <li>
                <a href="/faqs">FAQs</a>
              </li>
             
            </ul>
          </div>
          <div className="col-md-4">
            <h4 className="text-danger">Follow Us</h4>
            <div className="social-links">
              <a href="#" className="social-icon">
                <AiFillFacebook size={30} />
              </a>
              <a href="#" className="social-icon">
                <AiFillTwitterSquare size={30} />
              </a>
              <a href="#" className="social-icon">
                <AiFillInstagram size={30} />
              </a>
              <a href="#" className="social-icon">
                <AiFillLinkedin size={30} />
              </a>
            </div>
          </div>
        </div>
      </div>
      <div className="text-center py-1" style={{ borderTop: '1px solid #aaa', color: 'grey', fontSize: '14px' }}>
        All Rights Reserved &copy; Pizza Palace
      </div>
    </footer>
  );
}









