import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useEffect, useState } from 'react';
import {
  removeFromCart,
  increaseQuantity,
  decreaseQuantity,
} from '../actions/cartActions';

export default function Cartscreen() {
  const cartItems = useSelector((state) => state.cartReducer.cartItems) || [];

  const dispatch = useDispatch();
  const [subtotal, setSubtotal] = useState(0);
  const userLogin = useSelector((state) => state.loginUserReducer);

  useEffect(() => {
    if (!userLogin.currentUser) {
      window.location.href = '/login'; 
    }
  }, [userLogin]);

  useEffect(() => {
    const newSubtotal = cartItems.reduce(
      (x, item) => x + item.price * item.quantity,
      0
    );
    setSubtotal(newSubtotal);
  }, [cartItems]);

  const handleCheckout = () => {
    alert('Your Order Placed successfully');

    window.location.href = '/order';
  };

  return (
    <div>
      <div className="row justify-content-center page">
        <div className="col-md-6 mt-5">
          <h2 style={{ fontSize: '40px' }}>Your Orders</h2>
          {cartItems.length === 0 ? (
            <p>Your cart is empty.</p>
          ) : (
            <table className="table" style={{backgroundColor:'red !important'}}>
              <thead>
                <tr>
                  <th>Item Name</th>
                  <th>Price</th>
                  <th>Quantity</th>
                 {/*  <th>Actions</th> */}
                </tr>
              </thead>
              <tbody>
                {cartItems.map((item) => (
                  <tr key={item._id}>
                    <td>{item.name}</td>
                    <td>{item.price}</td>
                    <td>
                 
                      <b>{item.quantity}</b>
                     
                    </td>
                    
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
        <div className="col-md-4 text-right mt-3">
          <h2 className="text-center mt-5"   style={{ fontSize: '40px' }}>Subtotal: Rs. {subtotal}</h2>
      
        </div>
      </div>
    </div>
  );
}