export const addToCart = (pizza, quantity, varient) => (dispatch, getState) => {
  var cartItem = {
    name: pizza.name,
    _id: pizza._id,
    image: pizza.image,
    varient: varient,
    quantity: Number(quantity),
    prices: pizza.prices,
    price: pizza.prices[0][varient] * quantity,
  };
if (cartItem.quantity>10){
  alert("You can't add more then 10 quantities..")
}
else{
  if(cartItem.quantity<1){
    dispatch({ type: 'REMOVE_FROM_CART', payload: pizza });
  }
  else 
  dispatch({ type: 'ADD_TO_CART', payload: cartItem });
}

const cartItems=getState().cartReducer.cartItems
localStorage.setItem('cartItems', JSON.stringify(cartItems))
 /*  localStorage.setItem('cartItems', JSON.stringify(getState().cartReducer.cartItems)); */
};

export const removeFromCart = (itemId) => (dispatch, getState) => {
  dispatch({ type: 'REMOVE_FROM_CART', payload: itemId });
  localStorage.setItem('cartItems', JSON.stringify(getState().cartReducer.cartItems));
};

export const increaseQuantity = (itemId) => (dispatch, getState) => {
  dispatch({ type: 'INCREASE_QUANTITY', payload: itemId });
  localStorage.setItem('cartItems', JSON.stringify(getState().cartReducer.cartItems));
};

export const decreaseQuantity = (itemId) => (dispatch, getState) => {
  dispatch({ type: 'DECREASE_QUANTITY', payload: itemId });
  localStorage.setItem('cartItems', JSON.stringify(getState().cartReducer.cartItems));
};
