import React from 'react';
import { Link } from 'react-router-dom';

export default function FaqScreen() {
  return (
    <div id="faq-screen" style={{ backgroundColor: 'white', padding: '20px' }}>
      <div className="faq-section" style={{ marginBottom: '20px' }}>
        <h2 style={{textAlign: 'left', textJustify: 'inter-word' , fontSize: '24px', color: 'red', fontWeight: 'bold'}}>Why was my order voided/canceled?</h2>
        <p style={{ textAlign: 'left', textJustify: 'inter-word' }}>Here are some steps how you can order through our APP:

        <ul style={{ listStyleType: 'disc', marginLeft: '20px' }}>
            <li>Once you open the app, you will have the option to create an account or log in into your existing account.</li>
            <li>Click the products you want to order and then click Add to Cart, the red button at the bottom of the screen.</li>
            <li>You can review your order, check your subtotal and then click Proceed to Checkout.</li>
            <li>At the checkout, you can review and confirm all your order info (address, payment info, shipping type, and leave a tip for the driver).</li>
            <li>At the checkout, you can also select if you want to have the order for ASAP or if you want the order scheduled.</li>
            <li>Check the Order Details at the end of the checkout screen, and click Place Order.</li>
          </ul></p>
      </div>

      <div className="faq-section">
        <h2 style={{textAlign: 'left', textJustify: 'inter-word' ,  fontSize: '24px', color: 'red' , fontWeight: 'bold'}}>Is there a way to reorder my favorite order?</h2>
        <p style={{ textAlign: 'left', textJustify: 'inter-word' }}>We definitely don’t want your order to get voided, but if that happens, you will receive a cancellation notice on your email with all the details.
» Activate push notification by clicking on the Notification button under your Account, so you receive push notifications from the app as well.</p>
      </div>

      <div className="faq-section">
        <h2 style={{textAlign: 'left', textJustify: 'inter-word' ,  fontSize: '24px', color: 'red', fontWeight: 'bold'}}>What forms of payment do you accept?</h2>
        <p style={{ textAlign: 'left', textJustify: 'inter-word' }}>We accept all forms of prepaid payments (credit, debit, and prepaid cards issued by all major banks). We support PayPal, Google Pay and Apple Pay.</p>
      </div>

      <div className="faq-section">
        <h2 style={{ textAlign: 'left', textJustify: 'inter-word' , fontSize: '24px', color: 'red'}}>How can I cancel my order?</h2>
        <p style={{ textAlign: 'left', textJustify: 'inter-word' }}>
Feel free to start a chat with us or give us a call at 888-974-9928 and one of our agents will help with canceling your order. Canceling the order right after it was placed shouldn’t be a problem, however, our pizza partners might not be able to cancel it if they’ve started with the preparation of the food.</p>
      </div>
      
      <div className="faq-section">
        <h2 style={{ textAlign: 'left', textJustify: 'inter-word' , fontSize: '24px', color: 'red', fontWeight: 'bold'}}>My order was cancelled, when should I expect to receive back the funds?</h2>
        <p style={{ textAlign: 'left', textJustify: 'inter-word' }}>
        Refund - Once the Customer support representative issues a refund, it takes 3-5 business days for the money to reflect on your account, but this can vary according to the bank provider.
Void - We can void an order before the transaction is captured. This means that the transaction will be canceled and the pending charge for this payment will disappear from your statement in the next 12-24 business hours.</p>
      </div>
      <div className="back-to-home" style={{ textAlign: 'center', marginTop: '20px' }}>
      <Link to="/" style={{ textDecoration: 'none' }}>
          <button 
            style={{
              backgroundColor: 'red',
              color: 'white',
              padding: '10px 20px',
              border: 'none',
              borderRadius: '5px',
              fontSize: '16px',
              cursor: 'pointer',
            }}
          >
            Back to Home
          </button>
        </Link>
      </div>
    </div>
  );
}
